.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.gallery {
  margin-bottom: 1.5em;
}

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%;
}
.gallery-columns-2 .gallery-item {
  max-width: 50%;
}
.gallery-columns-3 .gallery-item {
  max-width: 33.33%;
}
.gallery-columns-4 .gallery-item {
  max-width: 25%;
}
.gallery-columns-5 .gallery-item {
  max-width: 20%;
}
.gallery-columns-6 .gallery-item {
  max-width: 16.66%;
}
.gallery-columns-7 .gallery-item {
  max-width: 14.28%;
}
.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}
.gallery-columns-9 .gallery-item {
  max-width: 11.11%;
}

.gallery-caption {
  display: block;
}

html {
  scroll-behavior: smooth;
}

/* Fixed header fix */
:target:before {
  content: "";
  display: block;
  height: 65px; /* same as your fixed header height */
  margin: -65px 0 0; /* negative fixed header height */
}

.admin-bar .fixed-top,
.admin-bar .sticky-top {
  top: 46px;
}
@media (min-width: 783px) {
  .admin-bar .fixed-top,
  .admin-bar .sticky-top {
    top: 32px;
  }
}

.mw-300 {
  max-width: 300px;
}

.mw-400 {
  max-width: 400px;
}

.mw-500 {
  max-width: 500px;
}

.mw-600 {
  max-width: 600px;
}

.mw-700 {
  max-width: 700px;
}

.mw-800 {
  max-width: 800px;
}

.mt-neg-200 {
  margin-top: -200px;
  position: relative;
  z-index: 1;
}

.pagination, .facetwp-pager,
.woocommerce-pagination {
  margin-top: 3rem;
  justify-content: center;
}

.comments-area {
  margin-top: 4.5rem;
}

.back-to-top {
  position: fixed;
  right: 1.5rem;
  bottom: -60px;
  width: 3rem;
  height: 3rem;
  padding: 0;
  opacity: 0;
  transition: all 0.2s ease-in-out;
  z-index: 10000;
  transform: rotate(270deg);
  background-image: url("../img/flecha-derecha.svg");
  background-size: 20px auto;
  background-position: center;
  background-repeat: no-repeat;
}

.scrolled .back-to-top {
  opacity: 0.9;
  bottom: 1.5rem;
}

#search-wrapper,
#error-404-wrapper {
  padding-top: 10rem;
}
#search-wrapper .page-header,
#error-404-wrapper .page-header {
  margin-bottom: 3rem;
}

#no-title-page-wrapper {
  padding-top: 0;
  padding-bottom: 0;
}

#wrapper-top-bar {
  padding: 0.25rem 0;
  font-size: 0.8rem;
  line-height: 1;
  border-bottom: 1px solid #A8A39D;
  height: 1.5rem;
}
#wrapper-top-bar p {
  margin-bottom: 0;
}

body.navbar-transparent #wrapper-navbar {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
}
body.navbar-transparent #wrapper-navbar + .wrapper {
  padding-top: 0;
}

body.scrolled #wrapper-navbar {
  box-shadow: 0 0.125rem 0.25rem rgba(31, 31, 31, 0.075);
}

body.privacy-policy #content, body.cmplz-document #content {
  max-width: 50rem;
  margin-bottom: 6rem;
}

.stretch-linked-block {
  position: relative !important;
  transition: opacity 0.15s linear;
}
.stretch-linked-block .editorskit-block-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 90;
}
.stretch-linked-block:hover {
  opacity: 0.7;
}
.stretch-linked-block:hover .wp-block-button.is-style-arrow-link .wp-block-button__link:after {
  right: 0;
}

.stretched-link:hover {
  text-decoration: none;
  color: #1f1f1f;
}

.entry-footer {
  position: relative;
  z-index: 2;
}

.ventajas-wrapper {
  padding: 1.5rem 0;
}
.ventajas-wrapper .slick-slide p {
  margin-bottom: 0;
  font-size: 1.25rem;
}
.ventajas-wrapper .slick-slide p img {
  display: inline-block;
  filter: brightness(0) invert(1);
  margin-right: 0.25em;
  margin-bottom: 0.25em;
}

.header-promos-container {
  height: 480px;
}

.header-promo .wp-block-cover,
.header-promos-sidebar .wp-block-cover {
  min-height: 480px;
  align-items: flex-end;
}

.header-promo .wp-block-cover {
  justify-content: flex-start;
}
.header-promo .wp-block-cover .has-background-dim {
  background-color: unset !important;
  background: linear-gradient(to top right, rgb(0, 0, 0), rgba(0, 0, 0, 0) 70%);
}
@media (min-width: 601px) {
  .header-promo .wp-block-cover .content-cover-title {
    font-size: 2.25rem;
  }
  .header-promo .wp-block-cover .wp-block-cover__background {
    opacity: 0 !important;
  }
}
.header-promo .wp-block-cover .wp-block-cover__image-background {
  object-position: 93% 50%;
}
.header-promo .wp-block-cover > .wp-block-cover__inner-container {
  max-width: 80%;
}
@media (min-width: 992px) {
  .header-promo .wp-block-cover > .wp-block-cover__inner-container {
    max-width: 66%;
  }
}

#wrapper-header-promos .wp-block-heading,
#wrapper-header-promos .header-promo-title {
  margin-bottom: 1rem;
}
#wrapper-header-promos .wp-block-buttons {
  margin-top: 0;
